body {
  background-color: var(--background);
  color: var(--main-color);
  margin: 0;
  padding: 0;
}

body[data-theme="light"] {
  --background: #f4f4f4;
  --main-color: #111321;
  --colorsun: #4b48db;
  --colormoon: #5f6775;
}

body[data-theme="dark"] {
  --background: #2e2e2e;
  --main-color: #fff;
  --colorsun: #5f6775;
  --colormoon: #4b48db;
}

.theme-switch {
  justify-content: center;
  align-items: center;
  display: flex;
}

.iconlight {
  color: var(--colorsun);
  cursor: pointer;
  font-family: Poppins;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.33;
}

.icondark {
  color: var(--colormoon);
  cursor: pointer;
  font-family: Poppins;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.33;
}

input[type="checkbox"] {
  width: 40px;
  height: 20px;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background-color: #fff;
  border: .6px solid #4b48db;
  border-radius: 20px;
  outline: none;
  margin: 0 12px;
  transition: all .5s;
  position: relative;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, .2);
}

input:checked[type="checkbox"] {
  background-color: #4b48db;
  border: 1px solid #fff;
}

input[type="checkbox"]:before {
  content: "";
  width: 16px;
  height: 16px;
  cursor: pointer;
  background-color: #4b48db;
  border-radius: 20px;
  transition: all .5s;
  position: absolute;
  top: 2px;
  left: 1px;
  transform: scale(1.1);
}

input:checked[type="checkbox"]:before {
  background-color: #fff;
  left: 21px;
}

.container {
  width: 100%;
  width: 1200px;
  margin: 0 auto;
  padding: 0 16px;
}

.list {
  list-style-type: none;
}

.link {
  text-decoration: none;
}

.header {
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  padding-top: 28px;
  padding-bottom: 28px;
}

.header__wrapper {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  color: var(--main-color);
  font-family: Manrope;
  font-size: 42px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.36;
}

.site-nav {
  align-items: center;
  gap: 100px;
  display: flex;
}

.site-nav__list {
  gap: 69px;
  display: flex;
}

.site-nav__link {
  color: var(--main-color);
  font-family: Manrope;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.38;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.site-nav__link:after {
  content: "";
  height: 4px;
  width: 0%;
  background-color: #4b48db;
  border-radius: 2px;
  transition: width .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  bottom: -10px;
  left: 0;
}

.site-nav__link--current:after, .site-nav__link:hover:after, .site-nav__:focus:after {
  width: 100%;
}

.site-nav__link:hover, .site-nav__link:focus {
  color: #4b48db;
}

.search {
  width: 288px;
}

.card-news__list {
  gap: 30px;
  display: flex;
}

.card-news__item {
  width: 395px;
  height: 675px;
  margin-bottom: 28px;
  list-style-type: none;
  position: relative;
}

.card-news__link {
  text-decoration: none;
}

.card-news__ovarlay {
  position: relative;
}

.card-news__category {
  width: 112px;
  height: 24px;
  text-align: center;
  color: #fff;
  background-color: rgba(68, 64, 247, .7);
  border-radius: 0 4px 4px 0;
  justify-content: center;
  align-items: center;
  font-family: Manrope, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  display: flex;
  position: absolute;
  top: 40px;
}

.card-news__button {
  width: 126px;
  height: 28px;
  text-align: center;
  color: var(--main-color);
  background-color: #fff;
  border: #fff;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  font-family: Manrope, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  display: flex;
  position: absolute;
  bottom: 12px;
  right: 8px;
}

.card-news__icon {
  fill: #fff;
}

.card-news__title {
  letter-spacing: -.01em;
  color: var(--main-color);
  margin-top: 20px;
  font-family: Manrope, sans-serif;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.38;
}

.card-news__text {
  color: var(--main-color);
  margin-top: 20px;
  margin-bottom: 20px;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.38;
}

.card-news__time {
  text-align: right;
  color: #4440f7;
  font-family: Manrope, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.36;
  display: inline-block;
}

.card-news__element {
  color: #4440f7;
  cursor: pointer;
  font-family: Manrope, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.36;
  text-decoration: none;
  position: absolute;
  right: 17px;
}

.date__item {
  cursor: pointer;
  width: 100%;
  text-align: left;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  padding-top: 60px;
  padding-bottom: 12px;
  list-style-type: none;
  transition: all .4s;
  display: flex;
}

.date__item:after {
  content: "⌄";
  float: right;
  justify-content: center;
  margin-top: 22px;
  margin-left: 20px;
  font-size: 13px;
  display: flex;
}

.active:after {
  content: "⌃";
}

.card-news__box {
  display: none;
}

.card-news__box.active {
  padding-top: 40px;
  list-style-type: none;
  display: block;
  overflow: hidden;
}

